import React from "react";
import { Button } from "react-bootstrap";
import "./Command.css";
function Command(props) {
  return (
    <div
      className="white-text"
      style={{
        marginTop: "1rem",
        padding: "2rem 0rem 2rem 0rem",
        backgroundColor: "rgba(25,25,25,0.5)",
        boxShadow: "1px 2px 3px #22252B, inset 2px 2px 2px #35393F",
        textAlign: "center",
      }}
    >
      <h4 style={{ fontSize: "1.2em", marginBottom: "1em" }}>
        Command Station
      </h4>
      <div>
        <Button className="cmd-btn">Precharge on</Button>
      </div>
      <div>
        <Button className="cmd-btn">Precharge off</Button>
      </div>
      <div>
        <Button className="cmd-btn">Levitate</Button>
      </div>
      <div>
        <Button className="cmd-btn">PWM</Button>
      </div>
      <div>
        <Button className="cmd-btn">Brakes actuate</Button>
      </div>
      <div>
        <Button className="cmd-btn">Brakes retract</Button>
      </div>
    </div>
  );
}

export { Command };
