import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import Thermometer from "react-thermometer";
import "../commonStyles.css";
import "./Thermometer.css";

function ThermometerComp(props) {
  const tempSections = [
    { name: "EMS", temp: props.temp1 },
    { name: "LIM", temp: props.temp2 },
  ];
  return (
    <Container
      className="sub-section white-text"
      style={{ width: "100%", marginTop: "2rem", padding: "2rem" }}
    >
      <h4>Temperature</h4>
      <Row>
        {tempSections.map((section) => (
          <Col className="sub-sub-section">
            <h6>{section.name}</h6>
            <Row className="thermometer">
              <Col className="thermometer-tube">
                <Thermometer
                  min={0}
                  max={100}
                  width={10}
                  height={70}
                  backgroundColor={"rgb(0,0,0,0.1)"}
                  fillColor={
                    section.temp < 20
                      ? "rgb(0,0,150)"
                      : section.temp < 35
                      ? "rgb(0,100,0)"
                      : section.temp < 50
                      ? "rgb(0,100,100)"
                      : section.temp < 65
                      ? "orange"
                      : "red"
                  }
                  current={section.temp ? section.temp : 10}
                />
              </Col>
              <Col style={{ textAlign: "left" }}>
                {section.temp ? section.temp : 0} C
              </Col>
            </Row>
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export { ThermometerComp };
