import React from "react";
import { BMS } from "../components/BMS/BMS";
import { Navbar } from "../components/Navbar/Navbar";
import { Speed } from "../components/Speed/Speed";
import { Levitation } from "../components/Levitation/Levitation";
import { Command } from "../components/Command/Command";
import { Position } from "../components/Position/Position";
import { LeftNav } from "../components/Command/LeftNav";
import { ThermometerComp } from "../components/Thermometer/Thermometer";
import { TabulatedData } from "../components/TabulatedData/TabulatedData";
import { Row, Col, Container } from "react-bootstrap";
import "./styles.css";

function Home(props) {
  const tempTestData = [
    [
      { temp: 20, voltage: 10 },
      { temp: 40, voltage: 9 },
      { temp: 70, voltage: 12 },
    ],
    [
      { temp: 30, voltage: 13 },
      { temp: 80, voltage: 19 },
      { temp: 10, voltage: 1 },
    ],
    [
      { temp: 20, voltage: 20 },
      { temp: 40, voltage: 11 },
      { temp: 70, voltage: 22 },
    ],
  ];
  return (
    <div>
      <Navbar />
      <Container fluid>
        <Row>
          <Col className="no-margin">
            <LeftNav />
          </Col>
          <Col lg={{ span: 8 }}>
            <Container style={{ marginLeft: "0px" }}>
              <Row style={{ margin: "0px" }}>
                <Col lg={4} className="col">
                  <BMS array={tempTestData} />
                </Col>
                <Col lg={3} className="col">
                  <Levitation height={props.height} />
                </Col>
                <Col lg={5} className="col">
                  <Speed speed={props.speed} />
                </Col>
              </Row>
              <Row style={{ margin: "0px" }}>
                <Col lg={8} className="col">
                  <Position position={props.position} />
                </Col>
                <Col lg={4} className="col">
                  <ThermometerComp
                    temp1={props.emsTemp}
                    temp2={props.limTemp}
                  />
                </Col>
              </Row>
              <Row style={{ margin: "0px" }}>
                <Col lg={4} className="col">
                  <TabulatedData title="Reed Sensors" data={[20, 30, 10, 10]} />
                </Col>
                <Col lg={4} />
                <Col lg={4} className="col">
                  <TabulatedData
                    title="Pressure Sensors"
                    data={[20, 30, 10, 10]}
                  />
                </Col>
              </Row>
            </Container>
          </Col>
          <Col className="no-margin">
            <Command />
          </Col>
        </Row>
      </Container>
    </div>
  );
}

// function Receive(props) {
